export default {
  name: 'HelloWorld',

  data: function() {
    return {
      'quote': InspirationalQuotes.getRandomQuote()
    }
  },

  methods: {
    updateQuote: function() {
      this.quote = InspirationalQuotes.getRandomQuote();
    }
  }
}
</script>

